<template>
  <div class="bedusedstate">
    <v-dialog v-model="dialog" width="auto">
      <!-- <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template> -->

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <!-- 床位入住 透過判斷bedProcess是livein來顯示入住作業-->
            <div v-if="bedProcess == 'livein'">
              <v-row class="align-center">
                <v-col cols="12" md="3">
                  <h2 class="subheading grey--text">
                    <span>預備入住床位: {{ list.name }}</span>
                  </h2>
                </v-col>
                <v-col cols="12" md="2">
                  <v-text-field
                    type="time"
                    label="入住時間設定"
                    v-model="nowtime"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="3" class="d-flex align-center">
                  <v-text-field
                    label="預約人姓名"
                    v-model="preorderName"
                    :readonly="this.list.preorderName.length > 0"
                  ></v-text-field>
                  <v-btn
                    small
                    class="success mx-1"
                    @click.stop="preorderBed('preorder')"
                    v-if="!this.list.preorderName"
                    >預約</v-btn
                  >
                  <v-btn
                    small
                    class="warning mx-1"
                    @click.stop="preorderBed('cancel')"
                    v-else
                    >取消預約</v-btn
                  >
                </v-col>
                <v-col cols="12" md="4">
                  <!-- 功能表 -->
                  <v-toolbar dense>
                    <v-text-field
                      hide-details
                      prepend-icon="mdi-magnify"
                      single-line
                      label="搜尋"
                      v-model="serachKey"
                    ></v-text-field>
                  </v-toolbar>
                </v-col>
              </v-row>

              <v-card>
                <v-card-text style="overflow: auto">
                  <v-row class="text-center" style="height: 65vh">
                    <v-col
                      v-ripple
                      cols="6"
                      sm="4"
                      md="3"
                      lg="2"
                      v-for="(item, index) in searchfilter"
                      :key="index"
                    >
                      <v-card
                        :class="[
                          { 'blue lighten-2 pa-1': item.sex == '男' },
                          { 'pink lighten-4 pa-1': item.sex == '女' },
                        ]"
                        @click.stop="runLiveIn(item)"
                      >
                        <v-card-title
                          ><h3>
                            {{ item.p_code }} [{{ item.bed_name }}] <br />
                            {{ item.name }}
                          </h3></v-card-title
                        >
                        <v-card-text class="pa-0">
                          <!-- <h2>{{item.p_code}} / {{item.name}}</h2> -->
                          <v-img
                            :src="man_pic"
                            v-if="item.sex == '男'"
                            class="text-left"
                          ></v-img>
                          <v-img
                            :src="women_pic"
                            v-if="item.sex == '女'"
                            class="text-left"
                          ></v-img>
                        </v-card-text>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </div>

            <!-- 退床或更換床位 -->
            <div v-if="bedProcess == 'outOrChange'" class="mt-3">
              <v-card>
                <v-card-title>
                  <v-row>
                    <v-col cols="4"
                      >床號:{{ list.name }} / 目前住民:{{ list.u_name }}</v-col
                    >
                    <v-col cols="8" class="d-flex align-center">
                      <v-text-field
                        label="日期設定"
                        type="date"
                        v-model="nowday"
                        class="mr-1"
                        hide-details
                      ></v-text-field>
                      <v-text-field
                        label="時間設定"
                        type="time"
                        v-model="nowtime"
                        hide-details
                      ></v-text-field>
                      <v-btn
                        class="primary mx-1"
                        @click="runLiveOut"
                        v-if="!list.takeadayoff && !list.behospitalized"
                      >
                        退床
                        <v-icon>mdi-account-arrow-right-outline</v-icon>
                      </v-btn>
                      <v-btn
                        class="success mx-1"
                        @click="runEvent('takeadayoff', '確認請假')"
                        v-if="!list.takeadayoff && !list.behospitalized"
                      >
                        請假
                        <v-icon>mdi-exit-run</v-icon>
                      </v-btn>
                      <v-btn
                        class="error mx-1"
                        @click="runEvent('behospitalized', '確認住院')"
                        v-if="!list.takeadayoff && !list.behospitalized"
                      >
                        住院
                        <v-icon>mdi-hospital-building</v-icon>
                      </v-btn>

                      <v-btn
                        class="success mx-1"
                        @click="disEvent('takeadayoff', '取消請假')"
                        v-if="list.takeadayoff"
                      >
                        取消請假
                        <v-icon>mdi-vanish</v-icon>
                      </v-btn>
                      <v-btn
                        class="error mx-1"
                        @click="disEvent('behospitalized', '取消住院')"
                        v-if="list.behospitalized"
                      >
                        取消住院
                        <v-icon>mdi-vanish</v-icon>
                      </v-btn>
                    </v-col>
                  </v-row>
                  <v-toolbar dense>
                    <v-text-field
                      hide-details
                      prepend-icon="mdi-magnify"
                      single-line
                      label="搜尋"
                      v-model="serachKey"
                    ></v-text-field>
                    <v-btn
                      outlined
                      color="primary"
                      class="mx-2"
                      @click.stop="showAll"
                      >全部顯示</v-btn
                    >
                    <v-btn outlined color="primary" @click.stop="showEmpty"
                      >空床顯示</v-btn
                    >
                  </v-toolbar>
                </v-card-title>
                <v-card-text>
                  <v-row class="text-center">
                    <v-col
                      cols="6"
                      sm="4"
                      md="3"
                      lg="2"
                      xl="1"
                      v-for="(item, index) in searchfilter"
                      :key="index"
                    >
                      <v-card
                        outlined
                        :class="[
                          { 'blue lighten-2': item.sex == '男' },
                          { 'pink lighten-4': item.sex == '女' },
                        ]"
                      >
                        <div class="d-flex justify-center">
                          <h3>床號:{{ item.name }} {{ item.u_name }}</h3>
                        </div>

                        <v-img
                          :src="
                            item.used == ''
                              ? nobody_pic
                              : item.sex == '男'
                              ? bed_man_pic
                              : bed_women_pic
                          "
                          class="text-left"
                        >
                          <div
                            class="d-flex justify-end align-end pa-2"
                            style="height: 100%"
                          >
                            <v-btn
                              class="warning"
                              @click.stop="runLiveChange(item)"
                            >
                              換床
                              <v-icon>mdi-account-convert-outline</v-icon>
                            </v-btn>
                          </div>
                        </v-img>
                      </v-card>
                    </v-col>
                  </v-row>
                </v-card-text>
              </v-card>
            </div>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <!-- {{viewData}} -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";
import datepicker from "@/components/DatePicker";
import dayjs from "dayjs";

export default {
  components: { datepicker },
  props: ["items"],
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      bedProcess: "", //入住或退床及其他設定
      showData: [], //目前畫面顯示的資料內容,items是床位傳過來的,另一個是讀取病歷取得的資料
      serachKey: "",
      man_pic:
        this.$store.state.base_url +
        "/images/juumin_kihon_daichou_card_man.png",
      women_pic:
        this.$store.state.base_url +
        "/images/juumin_kihon_daichou_card_woman.png",
      nobody_pic: this.$store.state.base_url + "/images/bed_nobody.png",
      bed_man_pic: this.$store.state.base_url + "/images/bed_man.png",
      bed_women_pic: this.$store.state.base_url + "/images/bed_women.png",
      nowday: dayjs().format("YYYY-MM-DD"), //日期暫存
      nowtime: dayjs().format("HH:mm:ss"), //時間暫存
      preorderName: "", //預約人姓名
    };
  },

  computed: {
    searchfilter() {
      if (this.showData.length > 0) {
        var keys = this.serachKey.split(" ");
        var str = "";

        return keys.reduce(function (prev, element) {
          return prev.filter((item) => {
            str = JSON.stringify(item).toUpperCase();
            return str.includes(element.toUpperCase());
          });
        }, this.showData);
      }
    },
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (data) {
      this.dialog = true;
      this.bedProcess = data.bedProcess;
      this.editProcess(data);
    });
  },

  methods: {
    editProcess(data) {
      if (data.bedProcess == "livein") {
        console.log("run livein");
        this.title = "床位入住作業-選擇住民";
        this.titleStyle = "font-weight-black warning lighten-2";
        this.getUserData();
      } else {
        console.log("run state change");
        this.title = "床位退床 / 換床 / 請假 /住院作業";
        this.titleStyle = "font-weight-black info lighten-2";
        this.showData = this.items;
      }

      this.oldList = data.item; //將頁面資料與父項的資料連結
      this.list = { ...data.item }; //複制一份實際修改資料內容
      this.preorderName = this.list.preorderName;
    },
    getUserData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有病歷
      this.axios
        .post("user/getAll/" + this.$store.state.databaseName)
        .then((rs) => {
          // 2021.3.17 調整為只顯示還未住床的病歷
          this.showData = rs.data.filter(i=>{
            return (i.bed_name == null || i.bed_name.length == 0)
          });
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    showAll() {
      this.viewData = [...this.items];
    },
    showEmpty() {
      var outputitems = [...this.items];
      this.viewData = outputitems.filter((item) => {
        return item.used === "";
      });
    },
    runLiveIn(item) {
      //入住床位作業
      //判斷是否已入住床位
      var repeat = this.items.filter((i) => {
        return i.u_p_code == item.p_code;
      });

      if (repeat.length > 0) {
        var pop = {
          msg: "不可重覆入住!!請再確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
        return false;
      }

      //建構需要的資料
      var postData = {
        b_snkey: this.list.snkey,
        u_snkey: item.snkey,
        start_day: item.in_date,
        start_time: this.nowtime,
      };

      //修改床位使用情況In
      this.dialog = false;
      //this.$store.commit("setLoadingOn"); //讀取畫面on

      this.axios
        .post(
          "bed/stateUpdateIn/" + this.$store.state.databaseName + "/bed",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state != 1) {
            alert("error");
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });

      //新增床位使用紀錄
      this.$store.commit("getTime"); //取得現在時間
      postData["create_man"] =
        this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/add/" + this.$store.state.databaseName + "/bed_used",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data.state == 1) {
            var pop = { msg: "入住作業完成", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.list = {};
            // this.dialog = false;
            this.$emit("getAllData");
            // this.$store.commit("setLoadingOff"); //讀取畫面off
            // this.$refs.form.resetValidation();
          }
        });
    },
    runLiveOut() {
      //退床作業
      this.$confirm("確認退床?").then((res) => {
        if (res) {
          //修改床位使用紀錄
          var postData = {
            b_snkey: this.list.snkey,
            u_snkey: this.list.u_snkey,
            end_day: this.nowday,
            end_time: this.nowtime,
          };

          this.$store.commit("getTime"); //取得現在時間
          postData["edit_man"] =
            this.$store.state.pData.username +
            "(" +
            this.$store.state.time +
            ")"; //執行動作的人,時間,內容

          //修改床位使用情況Out
          this.axios
            .post(
              "bed/stateUpdateOut/" + this.$store.state.databaseName + "/bed",
              qs.stringify(postData)
            )
            .then((rs) => {
              // console.log(rs.data)
              if (rs.data.state != 1) {
                alert("error");
                return false; //error stop
              }
            });

          this.axios
            .post(
              "bed_used/edit_liveout/" +
                this.$store.state.databaseName +
                "/bed_used",
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                var pop = { msg: "退床作業完成", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.dialog = false;
                this.$emit("getAllData");
              }
            });
        }
      });
    },
    //換床作業(new 可兩床對換版)
    runLiveChange(item) {
      // console.log(this.list); //目前選擇床位資訊
      console.log(this.list, item); //選擇要住進去的床位資訊

      this.$confirm("確認換床?").then((res) => {
        if (res) {
          //換床判斷 using為已住床兩床對換, 空值為單純移到空床
          if (item.used == "using") {
            //兩床對換 1.暫存兩床資料  2兩床各自寫入新資料  3.bed_used資料更動 item為床位目前資訊
            //1.
            var aData = this.list;
            var bData = item;

            //a->b
            this.$store.commit("getTime"); //取得現在時間
            var postData = {
              user_snkey: aData.u_snkey,
              new_bed_snkey: item.snkey,
              old_bed_snkey: aData.snkey,
              edit_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")(換床)(" +
                this.list.name +
                "->" +
                item.name +
                ")", //執行動作的人,時間,內容
            };

            this.axios
              .post(
                "bed/stateUpdateChange/" + this.$store.state.databaseName,
                qs.stringify(postData)
              )
              .then((rs) => {
                console.log(rs.data);
                if (rs.data.state == 1) {
                  console.log("a->b ok");
                  // var pop = { msg: "換床作業完成", type: true, theme: "error" };
                  // this.$store.commit("snackbar", pop);
                  // this.dialog = false;
                  // this.$emit("getAllData");
                }
              });

            //b->a
            this.$store.commit("getTime"); //取得現在時間
            var postData = {
              user_snkey: bData.u_snkey,
              new_bed_snkey: aData.snkey,
              old_bed_snkey: bData.snkey,
              edit_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")(換床)(" +
                this.list.name +
                "->" +
                item.name +
                ")", //執行動作的人,時間,內容
            };

            this.axios
              .post(
                "bed/stateUpdateChange/" + this.$store.state.databaseName,
                qs.stringify(postData)
              )
              .then((rs) => {
                console.log(rs.data);
                if (rs.data.state == 1) {
                  console.log("b->a ok");
                  var pop = { msg: "換床作業完成", type: true, theme: "error" };
                  this.$store.commit("snackbar", pop);
                  this.dialog = false;
                  this.$emit("getAllData");
                }
              });
          } else {
            //單純移到空床 1.原床位資料->新床位資料 2.原床位清空 3.bed_used 資料更動
            //1.
            this.$store.commit("getTime"); //取得現在時間
            var postData = {
              user_snkey: this.list.u_snkey,
              new_bed_snkey: item.snkey,
              old_bed_snkey: this.list.snkey,
              edit_man:
                this.$store.state.pData.username +
                "(" +
                this.$store.state.time +
                ")(換床)(" +
                this.list.name +
                "->" +
                item.name +
                ")", //執行動作的人,時間,內容
            };

            this.axios
              .post(
                "bed/stateUpdateChange/" +
                  this.$store.state.databaseName +
                  "/emptybed",
                qs.stringify(postData)
              )
              .then((rs) => {
                // console.log(rs.data);
                if (rs.data.state == 1) {
                  var pop = { msg: "換床作業完成", type: true, theme: "error" };
                  this.$store.commit("snackbar", pop);
                  this.dialog = false;
                  this.$emit("getAllData");
                }
              });
          }

          //判斷住院資料更動
          if (
            this.list.behospitalized == "true" ||
            this.list.takeadayoff == "true"
          ) {
            // console.log("run 住院中換床資料更動");
            var postData = {
              // snkey: this.list.behospitalized_snkey,
              b_snkey: item.snkey,
              edit_man: "",
            };

            if (this.list.behospitalized == "true") {
              postData.snkey = this.list.behospitalized_snkey;
              var datasheet = "behospitalized";
            } else {
              postData.snkey = this.list.takeadayoff_snkey;
              var datasheet = "takeadayoff";
            }

            //this.$store.commit("setLoadingOn"); //讀取畫面on
            this.axios
              .post(
                "general/edit/" +
                  this.$store.state.databaseName +
                  "/" +
                  datasheet,
                qs.stringify(postData)
              )
              .then((rs) => {
                // console.log(rs.data);
                if (rs.data.state == 1) {
                  // var pop = { msg: "作業完成", type: true, theme: "success" };
                  // this.$store.commit("snackbar", pop);
                  this.dialog = false;
                  // this.$emit("getAllData");
                  // this.$store.commit("setLoadingOff"); //讀取畫面off
                }
              });
          }

          //判斷休假資料更動
          if (item.behospitalized == "true" || item.takeadayoff == "true") {
            // console.log("run 住院中換床資料更動");
            var postData = {
              // snkey: this.list.behospitalized_snkey,
              b_snkey: this.list.snkey,
              edit_man: "",
            };

            if (item.behospitalized == "true") {
              postData.snkey = item.behospitalized_snkey;
              var datasheet = "behospitalized";
            } else {
              postData.snkey = item.takeadayoff_snkey;
              var datasheet = "takeadayoff";
            }

            //this.$store.commit("setLoadingOn"); //讀取畫面on
            this.axios
              .post(
                "general/edit/" +
                  this.$store.state.databaseName +
                  "/" +
                  datasheet,
                qs.stringify(postData)
              )
              .then((rs) => {
                // console.log(rs.data);
                if (rs.data.state == 1) {
                  // var pop = { msg: "作業完成", type: true, theme: "success" };
                  // this.$store.commit("snackbar", pop);
                  this.dialog = false;
                  // this.$emit("getAllData");
                  // this.$store.commit("setLoadingOff"); //讀取畫面off
                }
              });
          }

          // if (this.list.takeadayoff == "true") {
          //   console.log("run 休假中換床資料更動");
          // }
        }
      });
    },
    runEvent(event, msg) {
      //執行住院
      this.$confirm(msg + "?").then((res) => {
        if (res) {
          //新增事件資訊(請假or住院)
          this.$store.commit("getTime"); //取得現在時間
          var postData = {
            b_snkey: this.list.snkey,
            u_snkey: this.list.u_snkey,
            start_day: this.nowday,
            start_time: this.nowtime,
            create_man:
              this.$store.state.pData.username +
              "(" +
              this.$store.state.time +
              ")", //執行動作的人,時間,內容
          };

          //this.$store.commit("setLoadingOn"); //讀取畫面on
          this.axios
            .post(
              "general/add/" + this.$store.state.databaseName + "/" + event,
              qs.stringify(postData)
            )
            .then((rs) => {
              console.log(rs.data);
              if (rs.data.state == 1) {
                this.changeEvent(event, rs.data.snkey);
                // this.$store.commit("setLoadingOff"); //讀取畫面off
              }
            });
        }
      });
    },
    changeEvent(event, snkey) {
      //修改狀態
      if (event == "takeadayoff") {
        var postData = {
          snkey: this.list.u_snkey,
          takeadayoff_snkey: snkey,
          takeadayoff: true,
          edit_man: "",
        };
      } else {
        var postData = {
          snkey: this.list.u_snkey,
          behospitalized_snkey: snkey,
          behospitalized: true,
          edit_man: "",
        };
      }

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/edit/" + this.$store.state.databaseName + "/user",
          qs.stringify(postData)
        )
        .then((rs) => {
          // console.log(rs.data);
          if (rs.data.state == 1) {
            var pop = { msg: "作業完成", type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.dialog = false;
            this.$emit("getAllData");
            // this.$store.commit("setLoadingOff"); //讀取畫面off
          }
        });
    },
    disEvent(event, msg) {
      //取消事件
      this.$confirm(msg + "?").then((res) => {
        if (res) {
          //修改住民請假狀態
          if (event == "takeadayoff") {
            //取消請假
            var postData = {
              snkey: this.list.u_snkey,
              takeadayoff_snkey: "",
              takeadayoff: "",
              edit_man: "",
            };
          } else {
            //取消住院
            var postData = {
              snkey: this.list.u_snkey,
              behospitalized_snkey: "",
              behospitalized: "",
              edit_man: "",
            };
          }

          //this.$store.commit("setLoadingOn"); //讀取畫面on
          this.axios
            .post(
              "general/edit/" + this.$store.state.databaseName + "/user",
              qs.stringify(postData)
            )
            .then((rs) => {
              // console.log(rs.data)
              if (rs.data.state != 1) {
                // this.$store.commit("setLoadingOff"); //讀取畫面off
                alert("error");
              }
            });

          //修改請假or住院資訊
          this.$store.commit("getTime"); //取得現在時間
          var postData = {
            end_day: this.nowday,
            end_time: this.nowtime,
            edit_man: "",
          };
          if (event == "takeadayoff") {
            //請假
            postData["snkey"] = this.list.takeadayoff_snkey;
          } else {
            //住院
            postData["snkey"] = this.list.behospitalized_snkey;
          }

          this.axios
            .post(
              "general/edit/" + this.$store.state.databaseName + "/" + event,
              qs.stringify(postData)
            )
            .then((rs) => {
              if (rs.data.state == 1) {
                var pop = { msg: "取消作業完成", type: true, theme: "success" };
                this.$store.commit("snackbar", pop);
                this.dialog = false;
                this.$emit("getAllData");
                // this.$store.commit("setLoadingOff"); //讀取畫面off
              }

              //住院期間會計帳建立 (住院期間固定費用不計算!! 例如:10日住院,12日出院,計2日)
              if (event == "behospitalized") {
                console.log("會計帳建立 -> 住院");
                var postData = {
                  key: "snkey",
                  value: this.list.behospitalized_snkey,
                };
                //取得所有資料
                //this.$store.commit("setLoadingOn"); //讀取畫面on
                this.axios
                  .post(
                    "general/getByKey/" +
                      this.$store.state.databaseName +
                      "/behospitalized",
                    qs.stringify(postData)
                  )
                  .then((rs) => {
                    var behospitalizedData = rs.data[0]; //取得住院資訊
                    // console.log(behospitalizedData);

                    //取得該住民所有會計帳資料
                    this.axios
                      .post(
                        "accounting/getAccountingByUserSnkey/" +
                          this.$store.state.databaseName +
                          "/" +
                          behospitalizedData.u_snkey
                      )
                      .then((rs) => {
                        //取得符合的資料 1.固定費類,特別約定事項-1(電費)

                        var accountingData = rs.data.filter((i) => {
                          console.log(
                            behospitalizedData.start_day,
                            i.start_day,
                            i.kind,
                            i.price
                          );
                          return (
                            (i.kind.includes("固定費類") ||
                              i.kind.includes("特別約定事項-1")) &&
                            i.price > 0 &&
                            dayjs(behospitalizedData.start_day).format("MM") ==
                              dayjs(i.start_day).format("MM")
                          );
                        });

                        //住院實際計算天數
                        const date1 = dayjs(behospitalizedData.start_day);
                        const date2 = dayjs(behospitalizedData.end_day);
                        var feeDay = date2.diff(date1, "day") - 1;

                        this.$store.commit("getTime"); //取得現在時間
                        // //建構新的資料
                        var postData = accountingData.map((i) => {
                          var newFee =
                            parseInt(i.price) /
                            parseInt(
                              dayjs(behospitalizedData.start_day)
                                .endOf("month")
                                .format("DD")
                            );

                          return {
                            user_snkey: behospitalizedData.u_snkey,
                            fee_snkey: i.fee_snkey,
                            date: behospitalizedData.end_day,
                            start_day: behospitalizedData.start_day,
                            end_day: behospitalizedData.end_day,
                            quantity: 1,
                            price: -(feeDay * Math.floor(newFee)).toFixed(0),
                            note: `(A)住院期間扣除費 ${dayjs(
                              behospitalizedData.start_day
                            )
                              .add(1, "day")
                              .format("MM/DD")}~${dayjs(
                              behospitalizedData.end_day
                            )
                              .add(-1, "day")
                              .format("MM/DD")} (${feeDay}天)`,
                            create_man:
                              this.$store.state.pData.username +
                              "(" +
                              this.$store.state.time +
                              ")", //執行動作的人,時間,內容
                          };
                        });

                        //有資料才寫入
                        if (postData.length > 0) {
                          console.log("寫入住院相關折讓費用");
                          this.axios
                            .post(
                              "general/addMulti/" +
                                this.$store.state.databaseName +
                                "/accounting",
                              qs.stringify(postData)
                            )
                            .then((rs) => {
                              console.log(rs.data);
                              if (rs.data.state == 1) {
                                console.log("住院費用紀錄OK");
                                var pop = {
                                  msg:
                                    "取消作業完成; [住院相關折讓費用紀錄完成]",
                                  type: true,
                                  theme: "success",
                                };
                                this.$store.commit("snackbar", pop);
                                // this.$store.commit("setLoadingOff"); //讀取畫面off
                              }
                            });
                        }
                      });
                  });
              }
            });
        }
      });
    },
    //預約床位 /取消預約床位
    preorderBed(type) {
      console.log(type)
      if (!this.preorderName.length) {
        var pop = { msg: "未輸入預約人姓名", type: true, theme: "error" };
        this.$store.commit("snackbar", pop);
        return false;
      }

      let postData = {
        snkey: this.list.snkey,
        preorderName: this.preorderName,
      };
      if (type == "cancel") {
        var msg = "已取消預約"
        this.preorderName = postData.preorderName = "";
      }else{
        var msg = "預約成功"
      }

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "bed/editBySnkey/" + this.$store.state.databaseName + "/bed",
          qs.stringify(postData)
        )
        .then((rs) => {
          if (rs.data){
            var pop = { msg: msg, type: true, theme: "success" };
            this.$store.commit("snackbar", pop);
            this.dialog = false;
            this.$emit("getAllData");
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
  },
};
</script>

