<template>
  <div class="BedAdd">
    <v-dialog v-model="dialog" width="50vw">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess">mdi-plus-circle</v-icon>
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{title}}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="6">
                <v-text-field
                  label="床位名"
                  prepend-icon="mdi-bed-empty"
                  v-model="list.name"
                  :rules="emptyRules"
                  ref="name"
                  reauired
                ></v-text-field>
              </v-col>
              <v-col cols="12" md="6">
                <v-switch label="Hidden" color="blue" v-model="list.hidden" value="true"></v-switch>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType=='add'">確認新增</v-btn>
          <v-btn class="success" @click="editOK" v-if="processType=='edit'">確認修改</v-btn>
        </v-card-actions>
        <!-- <pre>{{list}}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import qs from "qs";

export default {
  data() {
    return {
      dialog: false,
      list: { name: "" }, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [v => !!v || "不可空白"]
    };
  },
  props: ["items"],
  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function(item) {
      this.dialog = true;
      this.editProcess(item);
    });
  },
  
  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增床位";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {};
    },
    editProcess(item) {
      //進入畫面時,執行的是修改作業
      this.processType = "edit";
      this.title = "修改床位資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.oldList = item; //將頁面資料與父項的資料連結
      this.list = { ...item }; //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        //判斷是否重覆
        var repeat = this.items.filter(item => {
          if (item.name == this.list.name) {
            return item;
          }
        });
        if (repeat.length > 0) {
          var pop = {
            msg: "床號重覆!!請再確認!!",
            type: true,
            theme: "warning"
          };
          this.$store.commit("snackbar", pop);
          return false;
        }
        //判斷是否重覆 end

        this.$store.commit("getTime"); //取得現在時間
        this.list["create_man"] =
          this.$store.state.pData.username + "(" + this.$store.state.time + ")"; //執行動作的人,時間,內容

        this.$api.add("bed",this.list)         
          .then(rs => {
            if (rs.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.list = {};
              this.dialog = false;
              this.$emit("getAllData");
            }
          });
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        this.$store.commit("getTime"); //取得現在時間
        
        var postData={
          snkey:this.list.snkey,
          name:this.list.name,
          hidden:this.list.hidden,
          edit_man:this.$store.state.pData.username + "(" + this.$store.state.time + ")", //執行動作的人,時間,內容
        }
        
        this.$api.post("bed",postData)
          .then(rs => {
            if (rs.state == 1) {
              var pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.dialog = false;

              for (var i in this.list) {
                //將各個欄位的資料透過一對一的去比對,就可以使vue對資料會有反應
                this.oldList[i] = this.list[i];
              }
            }
          });
      }
    }
  }
};
</script>