      v-if="showKind"
    >
      <template v-slot:default="props">
        <v-card>
          <v-card-title>
            總床位:{{ items.length }} / 使用中:{{ using_bed }} / 空床位:
            {{ empty_bed }}
            <v-spacer></v-spacer>
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('bedusedlist')"
              >顯示床位使用紀錄</v-btn
            >
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('takeadayofflist')"
              >顯示請假紀錄</v-btn
            >
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('behospitalizedlist')"
              >顯示住院紀錄</v-btn
            >
          </v-card-title>
          <v-card-text>
            <v-row class="text-center">
              <v-col
                v-ripple
                cols="6"
                sm="4"
                md="3"
                lg="2"
                xl="1"
                v-for="(item, index) in props.items"
                :key="index"
              >
                <v-card
                  outlined
                  :class="[
                    { 'blue lighten-2': item.sex == '男' },
                    { 'pink lighten-4': item.sex == '女' },
                  ]"
                >
                  <div>
                    <h3>床號:{{ item.name }}</h3>
                  </div>

                  <v-img
                    :src="man_pic"
                    v-if="item.sex == '男'"
                    class="text-left"
                    @click.stop="bedUsedState(item, 'outOrChange')"
                  >
                    <div
                      class="d-flex justify-end align-end"
                      style="height: 100%"
                    ></div>
                  </v-img>

                  <v-img
                    :src="women_pic"
                    v-if="item.sex == '女'"
                    class="text-left"
                    @click.stop="bedUsedState(item, 'outOrChange')"
                  >
                    <div
                      class="d-flex justify-end align-end"
                      style="height: 100%"
                    ></div>
                  </v-img>

                  <v-img
                    :src="nobody_pic"
                    v-if="item.used == ''"
                    class="text-left"
                    @click.stop="bedUsedState(item, 'livein')"
                  >
                    <div
                      class="d-flex justify-center align-center"
                      style="height: 100%"
                    >
                      <v-alert
                        class="warning white--text text-center"
                        v-if="item.preorderName"
                      >
                        <h2>
                          <p>已預約</p>
                          ({{ item.preorderName }})
                        </h2>
                      </v-alert>
                    </div>
                  </v-img>

                  <div class="d-flex justify-space-between">
                    <v-icon color="yellow" v-if="item.hidden == 'true'"
                      >mdi-eye-off-outline</v-icon
                    >
                    <v-btn
                      icon
                      small
                      class="success"
                      @click.stop="edit(item)"
                      v-if="auth.bed_edit_key"
                    >
                      <v-icon color="white">mdi-pencil</v-icon>
                    </v-btn>
                    <v-btn
                      icon
                      small
                      class="success"
                      color="white"
                      v-if="item.takeadayoff"
                      >請假中</v-btn
                    >
                    <v-btn
                      icon
                      small
                      class="error"
                      color="white"
                      v-if="item.behospitalized"
                      >住院中</v-btn
                    >
                    <v-btn
                      icon
                      class="red"
                      small
                      @click.stop="del(item)"
                      v-if="auth.bed_del_key"
                    >
                      <v-icon color="white">mdi-close</v-icon>
                    </v-btn>
                  </div>

                  <v-alert class="pa-0 ma-0">
                    <h4>{{ item.u_p_code }} / {{ item.u_name }}</h4>
                  </v-alert>
                </v-card>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </template>
    </v-data-iterator>

    <!-- 床位顯示2 -->
    <v-data-iterator
      :items="searchV2(viewDataV2)"
      :items-per-page.sync="itemsPerPageV2"
      :footer-props="{ itemsPerPageOptionsV2 }"
      v-if="!showKind"
    >
      <template v-slot:default="props">
        <v-card>
          <v-card-title>
            總床位:{{ items.length }} / 使用中:{{ using_bed }} / 空床位:
            {{ empty_bed }}
            <v-spacer></v-spacer>
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('bedusedlist')"
              >顯示床位使用紀錄</v-btn
            >
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('takeadayofflist')"
              >顯示請假紀錄</v-btn
            >
            <v-btn
              class="mx-2 float-right primary"
              @click.stop="goOtherPage('behospitalizedlist')"
              >顯示住院紀錄</v-btn
            >
          </v-card-title>

          <v-card-text>
            <div
              color="primary"
              v-for="(roomData, index) in props.items"
              :key="index"
            >
              <v-alert color="primary" outlined class="pa-2">
                <v-sheet
                  class="blue lighten-2 white--text pa-1"
                  elevation="5"
                  height="30"
                >
                  <h2>{{ roomData.name }}</h2>
                </v-sheet>

                <v-row class="text-center">
                  <v-col
                    v-ripple
                    cols="2"
                    v-for="(item, index) in roomData.data"
                    :key="index"
                  >
                    <v-card
                      outlined
                      :class="[
                        { 'blue lighten-2': item.sex == '男' },
                        { 'pink lighten-4': item.sex == '女' },
                      ]"
                    >
                      <div>
                        <h3>床號:{{ item.name }}</h3>
                      </div>

                      <v-img
                        :src="man_pic"
                        v-if="item.sex == '男'"
                        class="text-left"
                        @click.stop="bedUsedState(item, 'outOrChange')"
                      >
                        <div
                          class="d-flex justify-end align-end"
                          style="height: 100%"
                        ></div>
                      </v-img>

                      <v-img
                        :src="women_pic"
                        v-if="item.sex == '女'"
                        class="text-left"
                        @click.stop="bedUsedState(item, 'outOrChange')"
                      >
                        <div
                          class="d-flex justify-end align-end"
                          style="height: 100%"
                        ></div>
                      </v-img>

                      <v-img
                        :src="nobody_pic"
                        v-if="item.used == ''"
                        class="text-left"
                        @click.stop="bedUsedState(item, 'livein')"
                      >
                        <div
                          class="d-flex justify-center align-center"
                          style="height: 100%"
                        >
                          <v-alert
                            class="warning white--text text-center"
                            v-if="item.preorderName"
                          >
                            <h2>
                              <p>已預約</p>
                              ({{ item.preorderName }})
                            </h2>
                          </v-alert>
                        </div>
                      </v-img>

                      <div class="d-flex justify-space-between">
                        <v-icon color="yellow" v-if="item.hidden == 'true'"
                          >mdi-eye-off-outline</v-icon
                        >
                        <v-btn
                          icon
                          small
                          class="success"
                          @click.stop="edit(item)"
                          v-if="auth.bed_edit_key"
                        >
                          <v-icon color="white">mdi-pencil</v-icon>
                        </v-btn>
                        <v-btn
                          icon
                          small
                          class="success"
                          color="white"
                          v-if="item.takeadayoff"
                          >請假中</v-btn
                        >
                        <v-btn
                          icon
                          small
                          class="error"
                          color="white"
                          v-if="item.behospitalized"
                          >住院中</v-btn
                        >
                        <v-btn
                          icon
                          class="red"
                          small
                          @click.stop="del(item)"
                          v-if="auth.bed_del_key"
                        >
                          <v-icon color="white">mdi-close</v-icon>
                        </v-btn>
                      </div>

                      <v-alert class="pa-0 ma-0">
                        <h4>{{ item.u_p_code }} / {{ item.u_name }}</h4>
                      </v-alert>
                    </v-card>
                  </v-col>
                </v-row>
              </v-alert>
            </div>
          </v-card-text>
        </v-card>
      </template>
    </v-data-iterator>

    <!-- <pre>{{viewDataV2[0]}}</pre> -->
  </div>
</template>

<script>
import qs from "qs";
import popupadd from "@/views/main/BedAdd";
import bedstate from "@/views/main/BedState"; //改變床位使用情況的頁面
import dayjs from "dayjs";

export default {
  components: { popupadd, bedstate },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      itemsV2: [], //目前讀取進來的資料庫(V2版的樣式)
      viewData: [], //顯示的資料
      viewDataV2: [], //第二種顯示床位的資料
      itemsPerPageOptions: [12, 24, 36],
      itemsPerPage: 12,
      itemsPerPageOptionsV2: [5, 10, 15],
      itemsPerPageV2: 5,
      showKind: true,
      serachKey: "",
      edititem: {}, //修改中資料
      man_pic: this.$store.state.base_url + "/images/bed_man.png",
      women_pic: this.$store.state.base_url + "/images/bed_women.png",
      nobody_pic: this.$store.state.base_url + "/images/bed_nobody.png",
      using_bed: "",
      empty_bed: "",
      editMode: false, //控制顯示床位修改/刪除按鈕
      // bedHidden: true, //控制顯示隱藏床位,true -> 不顯示
      bedHidden: "", //控制顯示隱藏床位,true -> 不顯示
      bedProcess: "", //床位執行功能 目前使用livein 代表入住,change代表換床
      auth: this.$store.state.pData, //各畫面授權使用功能限定(載入放在mounted)
    };
  },

  watch: {
    viewData: function () {
      let rs = this.viewData.filter((item) => {
        return item.used == "using";
      });
      this.using_bed = rs.length; //取得使用中的床位數
      this.empty_bed = this.viewData.length - rs.length; //取得空床數
    },
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.bedHidden = this.$store.state.cData.sethidden;
    this.getAllData();
  },

  methods: {
    getAllData() {
      //this.$store.commit("setLoadingOn"); //讀取畫面on
      //取得所有資料
      this.$api
        .options("bed/getAll/" + this.$store.state.databaseName + "/bed")
        .then((rs) => {
          // console.log(rs.data)
          let getData = rs;
          //判斷要不要顯示隱藏的病床
          if (this.bedHidden == "true") {
            // console.log("run no show hidden");
            getData = rs.filter((item) => {
              return item.hidden == "";
            });
          }
          this.items = getData.sort((a, b) => (a.name > b.name ? 1 : -1));

          //第一種顯示床位的方式
          this.viewData = [...this.items];
          //第一種顯示床位的方式end

          //建構第二種顯示床位的方式
          var viewDataV2 = [...this.items];
          var roomArr = viewDataV2.map((i) => {
            return i.name.substr(0, 3);
          });
          roomArr = new Set(roomArr);
          roomArr = [...roomArr];

          this.itemsV2 = this.viewDataV2 = roomArr.map((roomName) => {
            var matchData = viewDataV2.filter((i) => {
              return i.name.includes(roomName);
            });
            return {
              name: roomName,
              data: matchData,
            };
          });

          //建構第二種顯示床位的方式end
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
    sortBy(data) {
      //依name排序
      return data.sort(function (a, b) {
        return a.name > b.name ? 1 : -1;
      });
    },
    edit(item) {
      //修改功能
      this.$refs.editProcess.$emit("fromParent", item);
    },
    del(delData) {
      //刪除功能
      this.$confirm("確認刪除?").then((res) => {
        delData.delman =
          this.$store.state.pData.username +
          "(" +
          dayjs().format("YYYY-MM-DD HH:mm:ss") +
          ")";
        var postData = {
          snkey: delData.snkey,
          tablename: "bed",
          info: JSON.stringify(delData),
        };

        if (res) {
          this.$api.delete("bed", postData).then((rs) => {
            console.log(rs.data);
            if (rs.data.state == 1) {
              var pop = { msg: "已刪除", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.getAllData();
            }
          });
        }
      });
    },

    searchV2(data) {
      var keys = this.serachKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, data);
    },
    showAll() {
      this.serachKey = "";
      this.viewData = [...this.items];
      this.viewDataV2 = [...this.itemsV2];
    },
    showEmpty() {
      var outputitems = [...this.items];
      this.viewData = outputitems.filter((item) => {
        return item.used === "";
      });

      var outputitemsV2 = [...this.itemsV2];
      this.viewDataV2 = outputitemsV2.filter((items) => {
        var matchcounts = items.data.filter((j) => {
          return j.used === "";
        });
        if (matchcounts.length > 0) {
          return items;
        }
      });
    },
    showTakeadayoff() {
      var outputitems = [...this.items];
      this.viewData = outputitems.filter((item) => {
        return item.takeadayoff === "true";
      });

      var outputitemsV2 = [...this.itemsV2];
      this.viewDataV2 = outputitemsV2.filter((items) => {
        var matchcounts = items.data.filter((j) => {
          return j.takeadayoff === "true";
        });
        if (matchcounts.length > 0) {
          return items;
        }
      });
    },
    showBehospitalized() {
      var outputitems = [...this.items];
      this.viewData = outputitems.filter((item) => {
        return item.behospitalized === "true";
      });

      var outputitemsV2 = [...this.itemsV2];
      this.viewDataV2 = outputitemsV2.filter((items) => {
        var matchcounts = items.data.filter((j) => {
          return j.behospitalized === "true";
        });
        if (matchcounts.length > 0) {
          return items;
        }
      });
    },
    goOtherPage(page) {
      this.$router.push("/Main/" + page);
    },
    changeHidden() {
      if (this.bedHidden == "true") {
        this.bedHidden = "false";
      } else {
        this.bedHidden = "true";
      }
      this.getAllData();
    },
    bedUsedState(item, state) {
      var postData = {
        bedProcess: state,
        item: item,
      };
      this.$refs.bedUsedState.$emit("fromParent", postData);
    },
  },
};
</script>


